.list-item {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1.2%;
    transition: height 0.4s linear, visibility 0.4s linear,;
    overflow: hidden;
}

.list-params {
    width: 95%;
    height: 100%;
    border-radius: 20px;
    color: #FFF;
    background-color: #01091ff2;
    transition: height 0.4s linear, visibility 0.4s linear;
}

.list-params2 {
    width: 100%;
    height: 50px;
    /* height: 100%; */
    /* border-radius: 20px; */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.expand-button {
    padding: 0px 11px;
    border: 1px solid #3d7eff;
    border-radius: 25px;
    cursor: pointer;
}
.expand-button:hover {
    background-color: #3d7eff;
}

.expanded {
    height: 230px
}

.expanded > .list-params {
    align-items: normal;
}

.expanded > .list-params > .list-params2 {
    height: 50px;
}

