:root {
    --ballSize: 10px;
    --pixelLimit: 15px;
    --width: 29px;
    --height: 14px;
}

.toggle-switch {
    position: relative;
    display: inline-block;
    width: var(--width);
    height: var(--height);
}

.toggle-switch input[type="checkbox"] {
    display: none;
}

.toggle-switch .switch {
    position: absolute;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #ccc;
    border-radius: 25px;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: background-color 0.2s ease;
}

.toggle-switch .switch::before {
    position: absolute;
    content: "";
    left: 1px;
    top: 1px;
    width: var(--ballSize);
    height: var(--ballSize);
    background-color: #aaa;
    border-radius: 50%;
    transition: transform 0.3s ease;
}

.toggle-switch input[type="checkbox"]:checked+.switch::before {
    transform: translateX(var(--pixelLimit));
    background-color: #FFF;
}