.slideIn {
    background-color: #3d7eff30;
    animation: menuSlide 0.5s 1;
    animation-fill-mode: forwards;
}

@keyframes menuSlide {
    from {
        transform: translateX(-100%);
        transition-timing-function: ease-in;
    }
    to {
        transform: translateX(0%);
        transition-timing-function: ease-out;
    }
}

.slideIn1 {
    background-color: #3d7eff30;
    animation: menuSlide1 0.5s 1;
    animation-fill-mode: forwards;
}

@keyframes menuSlide1 {
    from {
        transform: translateX(0%);
        transition-timing-function: ease-in;
    }
    to {
        transform: translateX(0%);
        transition-timing-function: ease-out;
    }
}

.slideIn1-3 {
    background-color: #3d7eff30;
    animation: menuSlide1-3 1s 1;
    animation-fill-mode: forwards;
}

@keyframes menuSlide1-3 {
    from {
        transform: translateX(-200%);
        transition-timing-function: ease-in;
    }
    to {
        transform: translateX(0%);
        transition-timing-function: ease-out;
    }
}

.slideOut {
    background-color: #3d7eff30;
    animation: menuSlideOut2 0.5s 1;
    animation-fill-mode: backwards;
}

@keyframes menuSlideOut2 {
    from {
        transform: translateX(100%);
        transition-timing-function: ease-in;
    }
    to {
        transform: translateX(0%);
        transition-timing-function: ease-out;
    }
}

.slideOut3-1 {
    background-color: #3d7eff30;
    animation: menuSlideOut3-1 1s 1;
    animation-fill-mode: backwards;
}

@keyframes menuSlideOut3-1 {
    from {
        transform: translateX(200%);
        transition-timing-function: ease-in;
    }
    to {
        transform: translateX(0%);
        transition-timing-function: ease-out;
    }
}





