@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap');

.svg-item {
    width: 100%;
    font-size: 16px;
    margin: 0 auto;
    animation: donutfade 1s;
    border-radius: 5px;
}

@keyframes donutfade {
    0% {
        opacity: .2;
    }
    100% {
        opacity: 1;
    }
}

@media (min-width: 992px) {
    .svg-item {
        width: 70%;
    }
}

.donut-ring {
    stroke-width: 2px;
}

.donut-segment {
    transform-origin: center;
    stroke: #FF6200;
}

.donut-segment-4 {
    stroke-linecap: round;
    stroke-width: 2px;
    transform: translateZ(0)
}

.segment-1{fill:#ccc;}
.segment-2{fill:aqua;}
.segment-3{fill:#d9e021;}
.segment-4{fill:#FF7DCBF2;}

/* .donut-percent {
    animation: donutfadelong 1s;
} */

@keyframes donutfadelong {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.donut-text {
    font-family: 'Montserrat', sans-serif;
    fill: #FF6200;
}

.donut-label {
    font-size: 0.28em;
    font-weight: 700;
    line-height: 1;
    fill: #000;
    transform: translateY(0.25em);
}

.donut-percent {
    font-size: 0.4em;
    line-height: 1;
    transform: translateY(0.5em);
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 500;
}

